import React from 'react';
import { useNavigate } from 'react-router-dom';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import '../css/slidebanner.css';

const SlideBanner = () => {
    const navigate = useNavigate();
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 3000,
    };

    const recommendations = [
        {
            image: 'https://png.pngtree.com/thumb_back/fh260/background/20230609/pngtree-three-puppies-with-their-mouths-open-are-posing-for-a-photo-image_2902292.jpg',
            title: '개인 선호도 프로필을 통한 추천 기능',
            description: '질문을 통해 사용자에게 추천해 드립니다.',
            link: "/recommend/question", // 추천 페이지로의 링크
        },
        {
            image: 'https://www.fitpetmall.com/wp-content/uploads/2023/10/image-26.png',
            title: '키워드 선택을 통한 추천 기능',
            description: '사용자가 선택한 키워드를 기반으로 추천해 드립니다.',
            link: "/recommend/keyword", // 추천 페이지로의 링크
        },
        {
            image: 'https://dimg.donga.com/wps/NEWS/IMAGE/2023/06/13/119746433.1.jpg',
            title: '보호 중인 동물 탐색',
            description: '현재 보호 중인 유기견들을 보실 수 있습니다.',
            link: "/animal",
        },
    ];

    return (
        <div className="recommend-slide">
            <Slider {...settings}>
                {recommendations.map((item, index) => (
                    <div key={index} className="slide-item">
                        <img src={item.image} alt={item.title} />
                        <h3>{item.title}</h3>
                        <p>{item.description}</p>
                        <button
                            className="slide-banner-button"
                            onClick={() => navigate(item.link)}
                        >
                            자세히 보기
                        </button>
                    </div>
                ))}
            </Slider>
        </div>
    );
};

export default SlideBanner;
